import React, { Component } from "react";
import { Navbar } from "react-bootstrap";
import { Nav } from "react-bootstrap";
import { NavDropdown } from "react-bootstrap";
import { Link } from "react-router-dom";
import "./project.css";

class Navbar1 extends Component {
  state = {
    pic1: require("./images/logo2.png"),
    pic2: require("./images/login.jpg"),
    pic3: require("./images/dial.png"),
  };
  render() {
    return (
      <div className="row-md-12" id="parent_menu">
        <div className="col-md-3" id="logo">
          <img id="logoimg" src={this.state.pic1} alt="..." />
        </div>
        <div className="col-md-6" id="menu">
          <ul id="ul_menu">
            <li className="nav-item" style={{ color: "black" }}>
              <Link className="nav-style" to="/home">Home</Link>
            </li>
            <li className="nav-item nav-style">
              <Nav className="me-auto ">
                <NavDropdown  
                  title="Avaliable cars"
                  id="collasible-nav-dropdown"
                  
                >
                  {/* <NavDropdown.Item  className="nav-style" href="#action/3.1">
                    Casual cars
                  </NavDropdown.Item> */}
                  <NavDropdown.Item href="#action/3.2"></NavDropdown.Item>
                  <NavDropdown.Item>
                    <Link  className="nav-style" to="sportscar"> Sports car</Link>
                  </NavDropdown.Item>
                  <NavDropdown.Divider />
                  <NavDropdown.Item>
                    {" "}
                    <Link  className="nav-style" to="landcruiser"> Land cruisors </Link>
                  </NavDropdown.Item>
                </NavDropdown>
              </Nav>
            </li>

            <li className="nav-item">
              <a className="nav-link">
                <Link  className="nav-style" to="cardetailinfo"> Car Details</Link>{" "}
              </a>
            </li>

            <li className="nav-item">
          
              <Link  className="nav-style" to="/login">Login/</Link>
              <Link  className="nav-style" to="/login">signup</Link>
            </li>
            <img id="loginimg" src={this.state.pic2} alt="..." />
          </ul>
        </div>

        <div className="col-md-3" id="login">
          <img id="dialimg" src={this.state.pic3} alt="..." />
          <p id="p_contact">
            {" "}
            Call us for help <button>Contact</button>
          </p>
        </div>
      </div>
    );
  }
}

export default Navbar1;
