import React, { Component } from 'react'

export class Cartpage extends Component {
    state = {
        Cart: [

        ],
        totalPrice: 0
    }

    handleClick = () => {
        this.setState({
            Cart: [...this.state.Cart,{
                id: 0,
                name: this.props.name,
                email: this.props.email,
                carName: this.props.carName,
                carReview: this.props.carReview,
                carPrice: this.props.carPrice,
            }],
            totalPrice: this.state.totalPrice + this.props.CarPrice,
        })
    }

    DeleteClicked = (object) => {
        this.setState(
            {
                Cart: this.state.Cart.filter((f) => f.id !== object.id)
            }
        )
    }

  render() {
    return (
      <div>

          <h1 style={{color: "aqua"}}>Add to Cart</h1>
          <table className={"table table-hover m-5 text-center"}>
           <thead>
                <tr>
                    <th scope='col'>#</th>
                    <th scope='col'>Name</th>
                    <th scope='col'>Email</th>
                    <th scope='col'>Car Name</th>
                    <th scope='col'>Price</th>
                    <th scope='col'>Review</th>
                    <th scope='col'>Action</th>                       
                </tr>   
           </thead>
           <tbody>
               {this.state.Cart.map((c) => {
                   return(
                    <tr>
                        <th scope='row'>{c.id}</th>
                        <td>{c.name}</td>
                        <td>{c.email}</td>
                        <td>{c.carName}</td>
                        <td>{c.carPrice}</td>
                        <td>{c.carReview}</td>
                        <td>
                            <button className="btn btn-danger" onClick={this.DeleteClicked.bind(this,c)}>Delete</button>
                        </td>
                    </tr>
                   );
               })}
           </tbody>     
          </table>
          <hr /><button className="btn btn-info" onClick={this.handleClick.bind(this)}>Add Cart</button>
      </div>
    )
  }
}

export default Cartpage;