import React, { Component } from 'react';

import {Carousel} from 'react-bootstrap';


class Carousal extends Component {
    state = {
        img1: require("./images/Kia.png"),
        img2: require("./images/kia2.png"),
        img3: require("./images/kia3.png")
        
      } 
    render()
       
     {
        return (
      <div id="carouselExampleSlidesOnly" className="carousel slide" data-ride="carousel" style={{width:"1481px",height:"400px",backgroundColor:"yellow"}}>
<Carousel>
  <Carousel.Item>
    <img
      className="d-block w-10"
      style={{width:"1481px",height:"400px"}}
      
      src={this.state.img1}
      alt="First slide"
    />
    
  </Carousel.Item>
  <Carousel.Item>
    <img
      className="d-block w-10 hero_image"
      style={{width:"1481px",height:"400px",}}
      src={this.state.img2}
      alt="Second slide"
    />

    
  </Carousel.Item>
 
  <Carousel.Item>
    <img
      className="d-block w-10"
      style={{width:"1481px",height:"400px"}}
      src={this.state.img3}
      alt="Second slide"
    />

    
  </Carousel.Item>
  
 </Carousel>

            </div>

        );
    }
       }
 
export default Carousal;