import { Button } from 'bootstrap';
import React, { Component } from 'react';
import Footer from './Footer';
class Cardetailinfo extends Component {

    state = { 

        img : require("./images/221.jpg")
     }
    render() { 
        return ( 
            <div>
            <div className='row'>
                <div className='col-md-12' style={{height:"100px", backgroundColor:"white"}}>
                    <h3 style={{marginLeft:"600px", marginTop:"30px"}}> Car Details</h3>
                </div>
            </div>



            <div className='row'>
                <div className='col-md-6' style={{height:"500px", backgroundColor:"white"}}>
                    <img src={this.state.img} style={{width:"80%", height:"80%", marginTop:"40px", marginLeft:"50px"}}/>
                </div>
                <div className='col-md-6' style={{height:"500px", backgroundColor:"white"}}>
                    <p style={{marginTop:"150px"}}>Changan is on a mission to build the vehicles that people want and need. By producing efficient, safe, stylish and affordable cars, Changan has earned the business of many satisfied customers, and the admiration of many happy employees. We are very grateful for our success and seek to give back to the world that has given us so much. <br>
                    </br> <br/> We want to make sure you always enjoy your shopping, ownership, and service experiences with Changan Automobile. Please share your questions and feedback with us. Below are the best ways to reach us wherever you might be.</p>

                 <button style={{ backgroundColor : "white" }} > Read More   <a href='http://www.globalchangan.com/'> </a></button> 
                </div>
            </div>
            <div>

                <Footer/>
            </div>
  
            </div>        
            

         );
    }
}
 
export default Cardetailinfo ;