import React from 'react'
import Cartpage from './Cartpage'
import { useLocation } from 'react-router-dom';

function Navigate() {
    const location = useLocation();
    const { name, email, carName, carPrice, carReview } = location.state;
  return (
    <div>
        <Cartpage name={name} email={email} carName={carName} carPrice={carPrice} carReview={carReview}/>
    </div>
  )
}

export default Navigate;