import React, { Component } from 'react';
import { getinfo } from './Carinfo';
import { Card } from 'react-bootstrap';
import "./project.css";
import { Link } from 'react-router-dom';

class Carinfo1 extends Component {
    state = { 
        data: getinfo(),
      } 
    render() { 
        return (



            <div className='col-md-6' id='card'>
                {
                    this.state.data.map(item=>(
                    <Card style={{ width: '40rem' }}>
                    <Card.Img variant="top" src={item.img} />
                    <Card.Body>
                      <Card.Title>{item.Name}</Card.Title>
                      <Card.Text>
                        {item.Price}
                      </Card.Text>
                      <Card.Text>
                        {item.Review}
                        </Card.Text>
                    <Link 
                    className={"btn btn-primary"}
                    to="/login"
                    state={{carName: item.Name,carPrice: item.Price,carReview: item.Review}}
                    >Buy Car
                    </Link>
                    </Card.Body>
                  </Card>

                        ))
                }
</div>
        );
    }
}
 
export default Carinfo1;