import {useFormik} from 'formik';
import {Link,useLocation} from 'react-router-dom';
import './Loginform.css' 

function Form() {

    const formik = useFormik({
        initialValues: {
            name: '',
            email: '',
            password: '',
        },
        onSubmit: values => {
            console.log('Form Values',values);
        },
        validate: values => {
            let errors = {
                
            }
            if(!values.name){
                errors.name = "*Required"
            }else if(!/^[a-zA-Z][a-zA-Z\\s]+$/.test(values.name)){
                errors.name = "*Must be Alphabetic"    
            }
            if(!values.email){
                errors.email = "*Required"
            }else if(!/^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(values.email)){
                errors.email = "*Invalid Email Format"
            }

            if(!values.password){
                errors.password = "*Required"
            }else if(!/^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$%^&*-]).{8,}$/.test(values.password)){
                errors.password = "*Minimum eight characters, at least one uppercase, at least one lower case, at least one digit, at least one special character"
            }
            return errors
        }
    });

    const location = useLocation();
    const { carName,carPrice,carReview } = location.state;
    
  return (
      <div>
            <div className="Forms shadow">
        <h2>Login !</h2>
        <form onSubmit={formik.handleSubmit}>
            
            <div className="form-control">
                <label htmlFor="name">Name</label>
                <input type="text" id="name" name="name" onChange={formik.handleChange} value={formik.values.name}/>
                {formik.errors.name? <div style={{color: "red"}}>{formik.errors.name}</div>: null}
            </div>
            <div className="form-control">
                <label htmlFor="email">E-mail</label>
                <input type="email" id="email" name="email" onChange={formik.handleChange} value={formik.values.email}/>
                {formik.errors.email? <div style={{color: "red"}}>{formik.errors.email}</div>: null}
            </div>
            <div className="form-control">
                <label htmlFor="password">Password</label>
                <input type="password" id="password" name="password" onChange={formik.handleChange} value={formik.values.password}/>
                {formik.errors.password? <div style={{color: "red"}}>{formik.errors.password}</div>: null}
            </div>
        </form>
        <hr />
        <Link 
        className='SubBut btn btn-info'
        to="/navigate" 
        state={{name: formik.values.name,email: formik.values.email,carName: carName,carPrice: carPrice,carReview: carReview}}
        >
            Submit
        </Link>
    </div>
      </div>
    
  )
}

export default Form;